<template>
<div id="user-panel">
    <section class="hero">
        <div class="hero-body">
          <div class="container">
            <img :src="info.avatar" alt="Placeholder image">
            <h1 class="title">
              {{info.nickname}} ({{ code }})
            </h1>

            <h2 class="subtitle">
              钱包地址：{{address}}
            </h2>
                        <h2 class="subtitle">
              签名：{{info.bio}}
            </h2>
          </div>
        </div>
      </section>

</div>
</template>

<script>
import { get } from '@/api'
// import request from 'superagent'
// import { compress } from '@/lib/lzma'
export default {
  name: 'User',
  // props: ['address'],
  computed: {
    address () {
      return this.$route.params['address']
    },
    code () {
      return this.address.slice(-6)
    }
  },
  data () {
    return {
      myCroppa: {},
      info: {

      }
    }
  },
  async created () {
    // TODO: 获取插件当前钱包地址，现在是 hard-encode 了我的头像
    const myInfo = await get(this.address)
    this.info = myInfo
  },
  methods: {

  }
}
</script>

<style scoped>
/* #upload {
    text-align: center
} */
#user-panel {
  text-align: center
}
</style>



